.App {
  text-align: center;
  margin-top: 50%;
  // padding-bottom: 54rem;
}

.charsDiv {
  display: flex;
  justify-content: center;
}

.col {
  margin: 0px !important;
  padding: 0px !important;
}

.row {
  margin-left: 5rem !important;
  margin-right: 5rem !important;
}

img {
  border-radius: 10px;
  margin-top: 20px;
}

.list-inline {
  display: inline-flex;
  list-style: none;
  padding: 0px;
  margin-top: 0px;
}

.list-inline-item {
  margin: 5px;
}

.fa {
  padding: 15px;
  font-size: 23px;
  width: 53px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 50%;
  color: #fff;
  background: black;
}

.fa:hover {
  color: black;
  background: white;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;
  // border: 1px solid red;
}
